"use client";

import { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Activity, Home, Search, FileText, Clock, CheckCircle, XCircle, AlertCircle } from "lucide-react";
import Link from "next/link";
import { Badge } from "@/components/ui/badge";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";

interface ClaimData {
  claimId: string;
  patientName: string;
  serviceDate: string;
  provider: string;
  amount: string;
  status: 'Paid' | 'Pending' | 'Denied' | 'Processing';
  lastUpdated: string;
}

const mockClaims: ClaimData[] = [
  {
    claimId: "CLM-2024-001234",
    patientName: "John Smith",
    serviceDate: "2024-01-15",
    provider: "City Medical Center",
    amount: "$450.00",
    status: "Paid",
    lastUpdated: "2024-01-20"
  },
  {
    claimId: "CLM-2024-001235",
    patientName: "Sarah Johnson",
    serviceDate: "2024-01-18",
    provider: "Downtown Clinic",
    amount: "$125.00",
    status: "Processing",
    lastUpdated: "2024-01-22"
  },
  {
    claimId: "CLM-2024-001236",
    patientName: "Michael Brown",
    serviceDate: "2024-01-20",
    provider: "Specialty Care Associates",
    amount: "$850.00",
    status: "Pending",
    lastUpdated: "2024-01-23"
  },
  {
    claimId: "CLM-2024-001237",
    patientName: "Emily Davis",
    serviceDate: "2024-01-12",
    provider: "Emergency Medical Services",
    amount: "$2,150.00",
    status: "Denied",
    lastUpdated: "2024-01-19"
  }
];

export default function ClaimsPage() {
  const [searchTerm, setSearchTerm] = useState("");
  const [statusFilter, setStatusFilter] = useState("all");
  const [claims, setClaims] = useState<ClaimData[]>(mockClaims);
  const [selectedClaim, setSelectedClaim] = useState<ClaimData | null>(null);

  const filteredClaims = claims.filter(claim => {
    const matchesSearch = claim.claimId.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         claim.patientName.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         claim.provider.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesStatus = statusFilter === "all" || claim.status.toLowerCase() === statusFilter;
    return matchesSearch && matchesStatus;
  });

  const getStatusIcon = (status: string) => {
    switch (status) {
      case 'Paid':
        return <CheckCircle className="w-4 h-4 text-green-600" />;
      case 'Processing':
        return <Clock className="w-4 h-4 text-blue-600" />;
      case 'Pending':
        return <AlertCircle className="w-4 h-4 text-yellow-600" />;
      case 'Denied':
        return <XCircle className="w-4 h-4 text-red-600" />;
      default:
        return <FileText className="w-4 h-4 text-gray-600" />;
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'Paid':
        return "bg-green-100 text-green-800";
      case 'Processing':
        return "bg-blue-100 text-blue-800";
      case 'Pending':
        return "bg-yellow-100 text-yellow-800";
      case 'Denied':
        return "bg-red-100 text-red-800";
      default:
        return "bg-gray-100 text-gray-800";
    }
  };

  const generatePDF = (claim: ClaimData) => {
    // Create a simple PDF generation function
    const printWindow = window.open('', '_blank');
    if (printWindow) {
      const htmlContent = `
        <!DOCTYPE html>
        <html>
        <head>
          <title>Claim Details - ${claim.claimId}</title>
          <style>
            body { font-family: Arial, sans-serif; margin: 20px; }
            .header { text-align: center; border-bottom: 2px solid #2563eb; padding-bottom: 10px; margin-bottom: 20px; }
            .section { margin-bottom: 20px; }
            .label { font-weight: bold; color: #1e40af; }
            .value { margin-left: 10px; }
            .status-badge { 
              display: inline-block; 
              padding: 4px 8px; 
              border-radius: 12px; 
              font-size: 12px; 
              font-weight: bold;
            }
            .paid { background-color: #dcfce7; color: #166534; }
            .processing { background-color: #dbeafe; color: #1e40af; }
            .pending { background-color: #fef3c7; color: #92400e; }
            .denied { background-color: #fee2e2; color: #991b1b; }
            .footer { margin-top: 40px; padding-top: 20px; border-top: 1px solid #e5e7eb; font-size: 12px; color: #6b7280; }
          </style>
        </head>
        <body>
          <div class="header">
            <h1>AVAILITY Healthcare Services</h1>
            <h2>Claim Details Report</h2>
            <p>Generated on ${new Date().toLocaleDateString()}</p>
          </div>

          <div class="section">
            <h3>Claim Information</h3>
            <p><span class="label">Claim ID:</span><span class="value">${claim.claimId}</span></p>
            <p><span class="label">Patient Name:</span><span class="value">${claim.patientName}</span></p>
            <p><span class="label">Service Date:</span><span class="value">${claim.serviceDate}</span></p>
            <p><span class="label">Provider:</span><span class="value">${claim.provider}</span></p>
            <p><span class="label">Claim Amount:</span><span class="value">${claim.amount}</span></p>
            <p><span class="label">Status:</span><span class="value"><span class="status-badge ${claim.status.toLowerCase()}">${claim.status}</span></span></p>
            <p><span class="label">Last Updated:</span><span class="value">${claim.lastUpdated}</span></p>
          </div>

          <div class="section">
            <h3>Additional Details</h3>
            <p><span class="label">Submitted Date:</span><span class="value">${new Date().toLocaleDateString()}</span></p>
            <p><span class="label">Processed By:</span><span class="value">AVAILITY Healthcare Portal</span></p>
            <p><span class="label">Contact:</span><span class="value">support@avality.com</span></p>
          </div>

          <div class="footer">
            <p>This document was generated automatically by the AVAILITY Healthcare Portal.</p>
            <p>For questions about this claim, please contact our support team.</p>
          </div>
        </body>
        </html>
      `;
      
      printWindow.document.write(htmlContent);
      printWindow.document.close();
      printWindow.print();
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 via-white to-blue-100">
      {/* Header */}
      <header className="bg-white/80 backdrop-blur-sm border-b border-blue-200 sticky top-0 z-50">
        <div className="container mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-4">
              <div className="w-10 h-10 bg-green-600 rounded-lg flex items-center justify-center">
                <FileText className="w-6 h-6 text-white" />
              </div>
              <div>
                <h1 className="text-2xl font-bold text-blue-900">AVAILITY</h1>
                <p className="text-sm text-blue-600">Claim Status</p>
              </div>
            </div>
            <Link href="/">
              <Button variant="outline" className="bg-blue-600 text-white hover:bg-blue-700 border-blue-600">
                <Home className="w-4 h-4 mr-2" />
                Home
              </Button>
            </Link>
          </div>
        </div>
      </header>

      <div className="container mx-auto px-4 py-8">
        <div className="max-w-7xl mx-auto">
          {/* Page Title */}
          <div className="text-center mb-8">
            <h1 className="text-3xl font-bold text-blue-900 mb-2">Claim Status Tracking</h1>
            <p className="text-gray-600">Track submitted claims, view payment details, and access claim history</p>
          </div>

          {/* Stats Cards */}
          <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
            <Card className="bg-white/80 backdrop-blur-sm border-2 border-green-200">
              <CardContent className="p-6">
                <div className="flex items-center justify-between">
                  <div>
                    <p className="text-sm text-gray-600">Paid Claims</p>
                    <p className="text-2xl font-bold text-green-600">
                      {claims.filter(c => c.status === 'Paid').length}
                    </p>
                  </div>
                  <CheckCircle className="w-8 h-8 text-green-600" />
                </div>
              </CardContent>
            </Card>

            <Card className="bg-white/80 backdrop-blur-sm border-2 border-blue-200">
              <CardContent className="p-6">
                <div className="flex items-center justify-between">
                  <div>
                    <p className="text-sm text-gray-600">Processing</p>
                    <p className="text-2xl font-bold text-blue-600">
                      {claims.filter(c => c.status === 'Processing').length}
                    </p>
                  </div>
                  <Clock className="w-8 h-8 text-blue-600" />
                </div>
              </CardContent>
            </Card>

            <Card className="bg-white/80 backdrop-blur-sm border-2 border-yellow-200">
              <CardContent className="p-6">
                <div className="flex items-center justify-between">
                  <div>
                    <p className="text-sm text-gray-600">Pending</p>
                    <p className="text-2xl font-bold text-yellow-600">
                      {claims.filter(c => c.status === 'Pending').length}
                    </p>
                  </div>
                  <AlertCircle className="w-8 h-8 text-yellow-600" />
                </div>
              </CardContent>
            </Card>

            <Card className="bg-white/80 backdrop-blur-sm border-2 border-red-200">
              <CardContent className="p-6">
                <div className="flex items-center justify-between">
                  <div>
                    <p className="text-sm text-gray-600">Denied</p>
                    <p className="text-2xl font-bold text-red-600">
                      {claims.filter(c => c.status === 'Denied').length}
                    </p>
                  </div>
                  <XCircle className="w-8 h-8 text-red-600" />
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Search and Filter */}
          <Card className="bg-white/80 backdrop-blur-sm border-2 border-blue-200 mb-8">
            <CardContent className="p-6">
              <div className="flex flex-col md:flex-row gap-4">
                <div className="flex-1">
                  <Label htmlFor="search" className="text-blue-900 mb-2 block">Search Claims</Label>
                  <div className="relative">
                    <Search className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                    <Input
                      id="search"
                      placeholder="Search by claim ID, patient name, or provider..."
                      value={searchTerm}
                      onChange={(e) => setSearchTerm(e.target.value)}
                      className="pl-10"
                    />
                  </div>
                </div>
                <div className="md:w-48">
                  <Label htmlFor="status" className="text-blue-900 mb-2 block">Filter by Status</Label>
                  <Select value={statusFilter} onValueChange={setStatusFilter}>
                    <SelectTrigger>
                      <SelectValue placeholder="All Statuses" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="all">All Statuses</SelectItem>
                      <SelectItem value="paid">Paid</SelectItem>
                      <SelectItem value="processing">Processing</SelectItem>
                      <SelectItem value="pending">Pending</SelectItem>
                      <SelectItem value="denied">Denied</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Claims Table */}
          <Card className="bg-white/80 backdrop-blur-sm border-2 border-blue-200">
            <CardHeader>
              <CardTitle className="flex items-center space-x-2">
                <FileText className="w-5 h-5 text-blue-600" />
                <span>Claims Overview</span>
                <Badge variant="outline">{filteredClaims.length} claims</Badge>
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="overflow-x-auto">
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>Claim ID</TableHead>
                      <TableHead>Patient Name</TableHead>
                      <TableHead>Service Date</TableHead>
                      <TableHead>Provider</TableHead>
                      <TableHead>Amount</TableHead>
                      <TableHead>Status</TableHead>
                      <TableHead>Last Updated</TableHead>
                      <TableHead>Actions</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {filteredClaims.map((claim) => (
                      <TableRow key={claim.claimId} className="hover:bg-blue-50/50">
                        <TableCell className="font-medium text-blue-900">{claim.claimId}</TableCell>
                        <TableCell>{claim.patientName}</TableCell>
                        <TableCell>{claim.serviceDate}</TableCell>
                        <TableCell>{claim.provider}</TableCell>
                        <TableCell className="font-medium">{claim.amount}</TableCell>
                        <TableCell>
                          <Badge className={getStatusColor(claim.status)}>
                            <div className="flex items-center space-x-1">
                              {getStatusIcon(claim.status)}
                              <span>{claim.status}</span>
                            </div>
                          </Badge>
                        </TableCell>
                        <TableCell>{claim.lastUpdated}</TableCell>
                        <TableCell>
                          <div className="flex space-x-2">
                            <Button
                              variant="outline"
                              size="sm"
                              onClick={() => setSelectedClaim(claim)}
                              className="text-blue-600 hover:text-blue-700"
                            >
                              View
                            </Button>
                            <Button
                              variant="outline"
                              size="sm"
                              onClick={() => generatePDF(claim)}
                              className="text-green-600 hover:text-green-700"
                            >
                              PDF
                            </Button>
                          </div>
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </div>

              {filteredClaims.length === 0 && (
                <div className="text-center py-8">
                  <FileText className="w-12 h-12 text-gray-400 mx-auto mb-4" />
                  <p className="text-gray-600">No claims found matching your search criteria.</p>
                </div>
              )}
            </CardContent>
          </Card>

          {/* Claim Details Modal */}
          {selectedClaim && (
            <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4 z-50">
              <Card className="bg-white max-w-4xl w-full max-h-[90vh] overflow-y-auto">
                <CardHeader>
                  <div className="flex items-center justify-between">
                    <CardTitle className="flex items-center space-x-2">
                      <FileText className="w-5 h-5 text-blue-600" />
                      <span>Claim Details - {selectedClaim.claimId}</span>
                    </CardTitle>
                    <div className="flex space-x-2">
                      <Button
                        variant="outline"
                        size="sm"
                        onClick={() => generatePDF(selectedClaim)}
                        className="text-green-600"
                      >
                        Generate PDF
                      </Button>
                      <Button
                        variant="outline"
                        size="sm"
                        onClick={() => setSelectedClaim(null)}
                      >
                        Close
                      </Button>
                    </div>
                  </div>
                </CardHeader>
                <CardContent className="space-y-6">
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                    <div>
                      <h3 className="font-semibold text-blue-900 mb-3">Claim Information</h3>
                      <div className="space-y-2 text-sm">
                        <div className="flex justify-between">
                          <span className="text-gray-600">Claim ID:</span>
                          <span className="font-medium">{selectedClaim.claimId}</span>
                        </div>
                        <div className="flex justify-between">
                          <span className="text-gray-600">Patient:</span>
                          <span className="font-medium">{selectedClaim.patientName}</span>
                        </div>
                        <div className="flex justify-between">
                          <span className="text-gray-600">Service Date:</span>
                          <span className="font-medium">{selectedClaim.serviceDate}</span>
                        </div>
                        <div className="flex justify-between">
                          <span className="text-gray-600">Provider:</span>
                          <span className="font-medium">{selectedClaim.provider}</span>
                        </div>
                        <div className="flex justify-between">
                          <span className="text-gray-600">Amount:</span>
                          <span className="font-medium">{selectedClaim.amount}</span>
                        </div>
                        <div className="flex justify-between">
                          <span className="text-gray-600">Status:</span>
                          <Badge className={getStatusColor(selectedClaim.status)}>
                            <div className="flex items-center space-x-1">
                              {getStatusIcon(selectedClaim.status)}
                              <span>{selectedClaim.status}</span>
                            </div>
                          </Badge>
                        </div>
                      </div>
                    </div>

                    <div>
                      <h3 className="font-semibold text-blue-900 mb-3">Additional Information</h3>
                      <div className="space-y-2 text-sm">
                        <div className="flex justify-between">
                          <span className="text-gray-600">Submitted Date:</span>
                          <span className="font-medium">{new Date().toLocaleDateString()}</span>
                        </div>
                        <div className="flex justify-between">
                          <span className="text-gray-600">Processed By:</span>
                          <span className="font-medium">AVAILITY Healthcare Portal</span>
                        </div>
                        <div className="flex justify-between">
                          <span className="text-gray-600">Contact:</span>
                          <span className="font-medium">support@avality.com</span>
                        </div>
                        <div className="flex justify-between">
                          <span className="text-gray-600">Last Updated:</span>
                          <span className="font-medium">{selectedClaim.lastUpdated}</span>
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className="flex space-x-4 pt-4">
                    <Button onClick={() => generatePDF(selectedClaim)} className="bg-green-600 hover:bg-green-700">
                      Download PDF Report
                    </Button>
                    <Button variant="outline" onClick={() => setSelectedClaim(null)}>
                      Close
                    </Button>
                  </div>
                </CardContent>
              </Card>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
